.product {
  width: 100%;
  height: auto;
  margin-top: 30px;
  margin-bottom: 30px;
}

.product-title {
  text-align: center;
  font-size: 32px;
  font-weight: 700;
  color: #37517e;
  margin-bottom: 30px;
}

.product-text {
  text-align: center;
  font-size: 16px;
  color: #444444;
}

.product-box {
  margin-top: 30px;
}

.categories ul {
  display: flex;
  align-items: center;
  gap: 30px;
}

.categories ul li {
  padding: 8px 20px;
  border-radius: 50px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.item-active {
  background: rgba(58, 156, 111, 1);
  color: #fff !important;
}

.product-box-card {
  margin-top: 30px;
}

.box {
  width: 1200px;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 29px;
}

.card-item {
  width: 278px;
  height: 415px;
  position: relative;
  overflow: hidden;
}
.card-item img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition: transform 0.5s ease;
}

.card-item-content {
  transition: transform 0.5s ease;
}

.card-item:hover img {
  transform: scale(1.2);
}

.card-item .hover-background {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 35px;
  background-color: rgba(55, 81, 126, 0.8);
  visibility: hidden;
  transition: visibility 0.3s ease;
}

@media (max-width: 1024px) {
  .product {
    max-width: 960px;
    margin: 0 auto;
  }
  .categories ul {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  .categories ul li {
    text-align: center;
  }
  .box {
    max-width: 960px;
    display: flex;
    flex-wrap: wrap;
    gap: 36px;
  }
  .card-item {
    width: 295px;
    height: 373px;
    position: relative;
    overflow: hidden;
  }
  .card-item-content {
    transition: transform 0.5s ease;
  }

  .card-item:hover img {
    transform: scale(1);
  }
  .image-container {
    width: 295px;
    height: 373px;
  }
  .image-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

