@media (max-width: 768px) {
  .product {
    max-width: 720px;
    width: 100%;
    margin: 0 auto;
  }
  .categories ul {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
  .categories ul li {
    text-align: center;
  }
  .box {
    max-width: 700px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
}

