.top-header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
  transition: 0.3s;
}
.header-scrolled {
  background: #f2f2f2;
  transition: 0.3s;
}
.header {
  padding: 20px 0;
}

.header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo a {
  font-size: 24px;
  color: #fff;
  margin: 0;
  padding: 0;
  line-height: 1;
  font-weight: 500;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.navs {
  display: flex;
  align-items: center;
  gap: 40px;
}

.navs ul {
  display: flex;
  align-items: center;
  gap: 10px;
}

.navs ul li a {
  padding: 8px 20px;
  border-radius: 50px;
  cursor: pointer;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
}

.navs ul li a:hover {
  color: #fff;
  transition: all 0.3s ease;
}

.active {
  color: #3a9c6f !important;
}

.navs .header-btn {
  width: 92px;
  height: 34px;
  box-sizing: border-box;
  padding: 8px 20px;
  font-size: 15px;
  border-radius: 50px;
  font-weight: 500;
  color: #fff;
}
.burger {
  position: relative;
  width: 40px;
  height: 30px;
  background: transparent;
  cursor: pointer;
  display: none;
}

.burger input {
  display: none;
}

.burger span {
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  background: #000 !important;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}

.burger span:nth-of-type(1) {
  top: 0px;
  transform-origin: left center;
}

.burger span:nth-of-type(2) {
  top: 50%;
  transform: translateY(-50%);
  transform-origin: left center;
}

.burger span:nth-of-type(3) {
  top: 100%;
  transform-origin: left center;
  transform: translateY(-50%);
}

.burger input:checked ~ span:nth-of-type(1) {
  top: 0;
  left: 5px;
  transform: rotate(45deg);
}

.burger input:checked ~ span:nth-of-type(2) {
  width: 0%;
  opacity: 0;
}

.burger input:checked ~ span:nth-of-type(3) {
  top: 28px;
  left: 5px;
  transform: rotate(-405deg);
}

